.header-container{display:flex;flex-direction:row;align-items:center;justify-content:space-between;background-color:#3f2a56;height:6rem;width:100%;border-bottom:solid 1px;border-color:#4f6070}.header-container .logo{margin-left:1rem;width:20rem;height:3rem}.header-container .links-container{display:flex;flex-direction:row;color:#fff;margin-right:2rem}.header-container .links-container .link{color:#fff;font-weight:600;margin:1rem}a{text-decoration:none}@media(max-width: 1100px){.header-container{display:flex;flex-direction:column;height:7rem;margin-right:0}.header-container .logo{padding-top:1rem}}